// src/features/navigation/components/nav-user.jsx
"use client"

import * as React from "react"
import {
  BadgeCheck,
  Bell,
  ChevronsUpDown,
  CreditCard,
  LogOut,
  Sparkles,
} from "lucide-react"

import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuGroup,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"
import {
  SidebarMenu,
  SidebarMenuButton,
  SidebarMenuItem,
  useSidebar,
} from "@/components/ui/sidebar"

/* -----------------------------------------------------------------------------
 * :   
 * ---------------------------------------------------------------------------*/

/**  () : 'Jane Doe' -> 'JD', 'LINE-01' -> 'L1'  */
function getInitials(name) {
  if (!name) return "?" //     
  const parts = String(name).trim().split(/[\s-_]+/).filter(Boolean)
  if (parts.length === 0) return name.slice(0, 2).toUpperCase()
  if (parts.length === 1) return parts[0].slice(0, 2).toUpperCase()
  return `${parts[0][0]}${parts[parts.length - 1][0]}`.toUpperCase()
}

/**   user       */
function normalizeUser(u) {
  if (!u || typeof u !== "object") return null
  const name = u.name ?? u.displayName ?? u.username ?? ""
  const email = u.email ?? u.mail ?? ""
  const avatar = u.avatar ?? u.photoUrl ?? u.image ?? ""
  // name/email       null 
  if (!name && !email) return null
  return { name, email, avatar }
}

/* -----------------------------------------------------------------------------
 * 
 * ---------------------------------------------------------------------------*/

/**
 * NavUser
 * -  ( )    +  
 * -   props    (  no-op)
 *
 * @param {Object} props
 * @param {Object} props.user               -   { name, email, avatar } ( , normalizeUser )
 * @param {Function} [props.onUpgrade]      - "Upgrade to Pro"  
 * @param {Function} [props.onAccount]      - "Account"  
 * @param {Function} [props.onBilling]      - "Billing"  
 * @param {Function} [props.onNotifications]- "Notifications"  
 * @param {Function} [props.onLogout]       - "Log out"  
 */
export function NavUser({
  user,
  onUpgrade = () => { },
  onAccount = () => { },
  onBilling = () => { },
  onNotifications = () => { },
  onLogout = () => { },
}) {
  const { isMobile } = useSidebar()

  //    (    )
  const normalized = React.useMemo(() => normalizeUser(user), [user])
  if (!normalized) return null

  const { name, email, avatar } = normalized
  const initials = getInitials(name || email || "U") //    

  return (
    <SidebarMenu>
      <SidebarMenuItem>
        <DropdownMenu>
          {/* :  + /  */}
          <DropdownMenuTrigger asChild>
            <SidebarMenuButton
              size="lg"
              aria-label="  "
              className="data-[state=open]:bg-sidebar-accent data-[state=open]:text-sidebar-accent-foreground"
            >
              <Avatar className="h-8 w-8 rounded-lg">
                {/*      AvatarFallback   */}
                <AvatarImage src={avatar} alt={name || email || "User avatar"} />
                <AvatarFallback className="rounded-lg">{initials}</AvatarFallback>
              </Avatar>

              <div className="grid flex-1 text-left text-sm leading-tight">
                {/* /    truncate  */}
                {name ? <span className="truncate font-medium">{name}</span> : null}
                {email ? <span className="truncate text-xs">{email}</span> : null}
              </div>

              <ChevronsUpDown className="ml-auto size-4" aria-hidden="true" />
            </SidebarMenuButton>
          </DropdownMenuTrigger>

          {/* :   +   */}
          <DropdownMenuContent
            className="w-(--radix-dropdown-menu-trigger-width) min-w-56 rounded-lg"
            side={isMobile ? "bottom" : "right"}
            align="end"
            sideOffset={4}
          >
            {/*   ( ) */}
            <DropdownMenuLabel className="p-0 font-normal">
              <div className="flex items-center gap-2 px-1 py-1.5 text-left text-sm">
                <Avatar className="h-8 w-8 rounded-lg">
                  <AvatarImage src={avatar} alt={name || email || "User avatar"} />
                  <AvatarFallback className="rounded-lg">{initials}</AvatarFallback>
                </Avatar>
                <div className="grid flex-1 text-left text-sm leading-tight">
                  {name ? <span className="truncate font-medium">{name}</span> : null}
                  {email ? <span className="truncate text-xs">{email}</span> : null}
                </div>
              </div>
            </DropdownMenuLabel>

            <DropdownMenuSeparator />

            {/*  :    onUpgrade      map    */}
            <DropdownMenuGroup>
              <DropdownMenuItem onSelect={onUpgrade}>
                <Sparkles className="mr-2 size-4" aria-hidden="true" />
                Upgrade to Pro
              </DropdownMenuItem>
            </DropdownMenuGroup>

            <DropdownMenuSeparator />

            {/*    */}
            <DropdownMenuGroup>
              <DropdownMenuItem onSelect={onAccount}>
                <BadgeCheck className="mr-2 size-4" aria-hidden="true" />
                Account
              </DropdownMenuItem>
              <DropdownMenuItem onSelect={onBilling}>
                <CreditCard className="mr-2 size-4" aria-hidden="true" />
                Billing
              </DropdownMenuItem>
              <DropdownMenuItem onSelect={onNotifications}>
                <Bell className="mr-2 size-4" aria-hidden="true" />
                Notifications
              </DropdownMenuItem>
            </DropdownMenuGroup>

            <DropdownMenuSeparator />

            {/*  */}
            <DropdownMenuItem onSelect={onLogout}>
              <LogOut className="mr-2 size-4" aria-hidden="true" />
              Log out
            </DropdownMenuItem>
          </DropdownMenuContent>
        </DropdownMenu>
      </SidebarMenuItem>
    </SidebarMenu>
  )
}
